<template>
  <div class="bg">
    <div class="row head-module">
      <button class="col-2" @click="updateContent('FreshModule')">
        Fresh module
      </button>
      <button class="col-2" @click="updateContent('Tapping')">Tapping</button>
      <button class="col-2" @click="updateContent('Overflow')">Overflow</button>
      <button class="col-2" @click="updateContent('Summary')">Summary</button>
    </div>
    <component :is="currentContent" />
  </div>
</template>

<script>
import FreshModule from './FreshModule.vue'
import Tapping from './TappingModule.vue'
import Overflow from './OverflowModule.vue'
import Summary from './Summary.vue'
export default {
  components: {
    FreshModule,
    Tapping,
    Overflow,
    Summary,
  },
  data() {
    return {
      currentContent: 'FreshModule',
    }
  },
  methods: {
    updateContent(content) {
      this.currentContent =
        content === 'FreshModule'
          ? 'FreshModule'
          : content === 'Tapping'
          ? 'Tapping'
          : content === 'Overflow'
          ? 'Overflow'
          : 'Summary'
    },
  },
}
</script>

<style>


.head-module {
  width: 100%;
  background-color: orange;
  height: 3rem;
  position: relative;
  left: 12px;
}
.head-module button {
  border: none;
  background-color: transparent;
}
.head-module button:hover {
  color: white;
}
</style>
